import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { RouterModule, Routes } from '@angular/router';
import { BasicModule } from '@app/shared/basic-components/basic.module';
import { LottieModule } from 'ngx-lottie';
import player from 'lottie-web';

import { HomeComponent } from './home/home.component';
import { TrainingGameBoardComponent } from './training-game-board/training-game-board.component';
import { VocbListComponent } from './vocb-list/vocb-list.component';
import { VocabCreateComponent } from './vocab-create/vocab-create.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { VocabDetailResolver } from './vocab-detail.reslover';

const routes: Routes = [
  {
    path: '',
    component: HomeComponent,
  },
  {
    path: 'game-board/:type',
    component: TrainingGameBoardComponent,
  },
  {
    path: 'game-board/vocab/:qcode',
    component: VocbListComponent,
  },
  {
    path: 'game-board/vocab/:qcode/create',
    component: VocabCreateComponent,
  },
  {
    path: 'game-board/vocab/:qcode/create/:questionId',
    component: VocabCreateComponent,
    resolve: {
      data: VocabDetailResolver
    }
  }
]


export function playerFactory() {
  return player;
}

@NgModule({
  declarations: [HomeComponent, TrainingGameBoardComponent, VocbListComponent, VocabCreateComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    BasicModule,
    LottieModule.forRoot({ player: playerFactory }),
    FormsModule,
    ReactiveFormsModule

  ],
  providers:[
    VocabDetailResolver
  ]
})
export class TrainingGameModule { }
