import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CommonService, TableListService } from '@app/core/http/serviceIndex';
import { TableFilter } from '@app/shared/models/table-filter';
import { Vocab, VocabList } from '@app/shared/models/vocab';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-vocb-list',
  templateUrl: './vocb-list.component.html',
  styleUrls: ['./vocb-list.component.css']
})
export class VocbListComponent implements OnInit {
  public tableFilter=<TableFilter>{};
  public vocabs:Vocab[];
  private subscription:Subscription;
  private isAsc:boolean=false;
  public isLoading:boolean=false;
  public qcode:string;
  public moduleName:string;
  
  constructor(private _activeRoute:ActivatedRoute,
    private _tableService:TableListService,private _cservice:CommonService) { }

  ngOnInit() {
    this.qcode = this._activeRoute.snapshot.paramMap.get("qcode");
    this.moduleName=this._cservice.getVocabName(this.qcode);
    this._activeRoute.queryParams.subscribe(
      (params) => {
        if (params.page) {
          this.tableFilter.page = params.page;
          this.tableFilter.limit = params.limit;
        } else if (params.search) {
          this.tableFilter.search = params.search;
        } else{
          this.tableFilter.page = 1;
          this.tableFilter.limit = 10;
          this.tableFilter.sortDirection = 'desc';
          this.tableFilter.sortColumn = 'name';
        }
        this.getVocabList();
      }
    )

  }

  getVocabList() {
    this.isLoading = true;
    this.subscription = this._tableService.getVocabList(this.tableFilter,this.qcode)
      .subscribe((res:VocabList) => {
        this.vocabs=res.questions;
        this.tableFilter = res.filter;
        this.isLoading = false;
      });
  }

  deleteVocab(data){
    this._tableService.onDeleteVocab(data.questionId, this.qcode)
      .subscribe((res) => {
        this.getVocabList();
      })
  }

  fitter(sortColumn) {
    this.isAsc = !this.isAsc ? this.isAsc = true : this.isAsc = false;
    this.tableFilter.sortDirection = this.isAsc ? 'asc' : 'desc';
    this.tableFilter.sortColumn = sortColumn;
    this.getVocabList();
  }

}
