import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DetailService } from '@app/core/http/detail.service';
import { AnimationOptions } from 'ngx-lottie';

@Component({
  selector: 'app-training-game-board',
  templateUrl: './training-game-board.component.html',
  styleUrls: ['./training-game-board.component.css']
})
export class TrainingGameBoardComponent implements OnInit {
  public moduleList:any=[];
  
  options: AnimationOptions = {
    path: 'assets/icon/lottie/questions-pen.json',
  };

  constructor(private _activeRoute:ActivatedRoute,private _service:DetailService) { }

  ngOnInit() {
    this.checkType();
  }

  checkType(){
    let type=this._activeRoute.snapshot.paramMap.get("type");
    if(type=='vocab'){
      this._service.getCheckProgressVocab().subscribe((res)=>{
        this.moduleList=res.questionBank;
      })
    }
  }

}
